import { Controller } from '@ali/4ever-cangjie';
import getBlockquoteState from '../utils/getBlockquoteState';
import isBlockquoteDisabled from '../utils/isBlockquoteDisabled';
import disableQuote from './disableQuote';
import enableQuote from './enableQuote';

export default function toggleQuote(editor: Controller, next?) {
  if (!isBlockquoteDisabled(editor)) {
    const isBlockquoteExist = getBlockquoteState(editor);
    if (isBlockquoteExist) {
      disableQuote(editor, next);
    } else {
      enableQuote(editor, next);
    }
  }

  if (next) {
    next();
  }

  return editor;
}
